<template>
	<section>
		<div class="button" @click="open">√∂ffnen</div>
		<div class="button" @click="logout">abmelden</div>
	</section>
</template>

<script>
const firebase = require('firebase/app')
require('firebase/auth')
export default {
	methods: {
		logout() {
			this.$emit('navigation', 'Login')
		},
		open() {
			console.log('open')

		}
	}
}
</script>

<style lang="scss" scoped>
	section {
		// height: 100%;
		background: #fff;
		
		display: flex;
		flex-direction: column;
		> * {
			flex-grow: 1;
		}
		> *:not(:last-child) {
			border-bottom: 1px solid #000;
		}
	}

	@media (orientation: landscape) {
		section {
			flex-direction: row;

			> *:not(:last-child) {
				border-right: 1px solid #000;
				border-bottom: none;
			}
		}
	}
</style>



